.form-log-in {
    background-color: #fff;
    padding: 30px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    max-width: 380px;
    box-sizing: border-box;
    &__title {
        @extend %h1;
        margin-bottom: 18px;
        margin-top: 8px;
    }
    &__email {
        margin-bottom: 10px;
    }
    &__submit {
        margin-bottom: 28px;
        margin-top: 22px;
    }
    &__registered {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__text {
        @extend %font-body;
        color: #000000;
    }
}